	import React from 'react';
	import Card from './Card';


	const CardList = ({pokes}) => {
		return (
	       <div>
	         {
	         	 pokes.map((user, i) => {
		           return (
			          <Card 
			          key={i}
			          id={pokes[i].id} 
			          name={pokes[i].name} 
			          attack={pokes[i].attack}
			          photo={pokes[i].photo}

			          />
			          );
		       })
	       	}
	       	</div>
		 );

	}

	export default CardList;