import React, { Component } from 'react';
import SearchBox from './components/SearchBox';
import {pokes} from './pokes';
import CardList from './components/CardList.js'
import Scroll from './components/Scroll.js'
// import ErrorBoundry from '../components/ErrorBoundry.js'

class App extends Component {
  constructor(){
        super()
        this.state = {
          pokes: [],
          searchfield: ''
  
        }
}


    componentDidMount() {
     
      this.setState({pokes: pokes});
    }



onSearchChange = (event) => {
      this.setState({ searchfield: event.target.value})
    }

 render() {
      const filterPokes = this.state.pokes.filter(pokes =>{
        return pokes.name.toLowerCase().includes(this.state.searchfield.toLowerCase())
      })
      return (
           <div className='tc'>
           <h1 className='courier fw6 f1 white'> POKEMON </h1>
          <SearchBox searchChange={this.onSearchChange}/>
           <Scroll>
           <CardList pokes={filterPokes}/>
           </Scroll>
            </div>
        );
    }
    }

  
  export default App;
